<script>
import reuqest from "@/utils/request";
export default {
    components: {

    },
    name: "HelloWorld",
    data() {
        return {
			aboutimg:"",
			text:''
		};
	},
	methods:{

	},
	created(){
		// 获取about
        reuqest({
        	url: '/api/getArticleDetaild',
			method: "post",
			data:{
				type: 'about'
			}
        }).then(data=>{
			var str = data.data.data.content
			// console.log(data.data.data.content)
			if(data.status==200){
				var str = data.data.data.content
				var imgReg = /<img.*?(?:>|\/>)/gi;
				var regex = "p.*?>(.*?)</p>"
				var srcReg = /src=[\'\"]?([^\'\"]*)[\'\"]?/i;
				var arr = str.match(imgReg);  // arr 为包含所有img标签的数组
				
				
				for (var i = 0; i < arr.length; i++) {
				var src = arr[i].match(srcReg);
				let p  = str[i].match(regex);
				//获取图片地址
				// console.log(src[1]);
				this.aboutimg = src[1];
				}
			}
        })
	}
};
</script>

